.querer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: 'Winter', sans-serif;
  gap: 10px;
}

@media (max-width: 768px) { /* Para pantallas más pequeñas */
  .querer {
    padding-top: 10px;
    gap: 5px;
  }
}