.amar {
	padding-top: 15px;
	font-family: 'Winter', sans-serif;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 10px;
}

.calculadora {
	width: 400px;
	height: 600px;
	padding: 14px;
	background-color: #1b1b32;
	border-radius: 20px;
	border: 5px solid white;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
}

.fila {
	margin: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
}